<!--suppress HtmlUnknownTag, HtmlUnknownAttribute, CheckEmptyScriptTag -->
<template>
  <v-container>
    <h2>Contact page admin zone</h2>

    <v-row dense>
      <v-col cols="12" md="6">
        <Notifications :alerts="alerts" />
      </v-col>

      <v-col cols="12" md="6">
        <v-card outlined
                v-if="!!markForDeletionArr.length">
          <v-card-text>
            Marked {{ markForDeletionArr.length }} items
            &nbsp;&nbsp;
            <v-btn @click="clearSelection()" >
              Cancel
            </v-btn>
            &nbsp;&nbsp;
            <v-btn color="error"
                   :disabled="!!restCallsGoing"
                   @click="deleteSelected()" >
              Delete
            </v-btn>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <!-- https://vuetifyjs.com/en/api/v-data-table/ -->
    <!-- https://vuetifyjs.com/en/components/data-tables/#dense -->
    <v-data-table dense
                  :disable-filtering="true"
                  :disable-sort="true"
                  :headers="headers"
                  :items="data"
                  :item-class="itemClass"
                  :page.sync="page"
                  :items-per-page.sync="itemsPerPage"
                  :footer-props="{itemsPerPageOptions: [7, 21, 49]}"
                  :server-items-length="totalItems"
                  :loading="!!restCallsGoing" >

      <!--suppress XmlUnboundNsPrefix -->
      <template v-slot:item.viewIcon="{item}">
        <v-icon style="cursor:pointer"
                @click="overlayId = item.effectiveId" >
          mdi-eye
        </v-icon>
      </template>

      <!--suppress XmlUnboundNsPrefix -->
      <template v-slot:item.fileName="{item}">
        <a v-if="item.fileName"
           :href="computeDownloadHref(item.effectiveId)"
           download >{{ item.fileName }}</a>
      </template>

      <!--suppress XmlUnboundNsPrefix -->
      <template v-slot:item.deleteIcon="{item}">
        <v-icon style="cursor:pointer"
                v-if="markForDeletionArr.indexOf(item.effectiveId) > -1"
                @click="markForDeletionSet.delete(item.effectiveId); markForDeletionArr = Array.from(markForDeletionSet)" >
          mdi-refresh
        </v-icon>
        <v-icon style="cursor:pointer"
                v-else
                @click="markForDeletionSet.add(item.effectiveId); markForDeletionArr = Array.from(markForDeletionSet)" >
          mdi-trash-can
        </v-icon>
      </template>

    </v-data-table>

    <v-overlay opacity="0.975"
               :value="!!overlayId">
      <h3 v-if="!overlayContent">Loading {{ overlayId }}...</h3>
      <div v-else style="overflow:auto; max-height: 95vh">

        <h3 v-if="overlayContent.title">{{ overlayContent.title }}</h3>
        <h3 v-else>(No Title)</h3>

        <p style="opacity: 0.5">{{ overlayContent.effectiveId }}</p>

        <v-textarea v-model="overlayContent.content" auto-grow :readonly="true" outlined />

        <a v-if="overlayContent.fileName"
           :href="computeDownloadHref(overlayContent.effectiveId)"
           > {{ overlayContent.fileName }} ({{ overlayContent.fileSize }}B)</a>

        <p style="opacity: 0.75">Submitted {{ overlayContent.created }} by {{ overlayContent.submitter }} via {{ overlayContent.receivedFrom }}</p>
      </div>

      <v-btn @click="overlayId = null">Close</v-btn>
    </v-overlay>

  </v-container>
</template>

<script>
  import router from '../plugins/router'
  import AxiosHelper from '../components/AxiosHelper.vue';
  import Notifications from '../components/Notifications.vue';

  export default {
    components: {Notifications},
    mixins: [AxiosHelper],

    data: () => ({
        data: [],
        totalItems: 0,

        headers: [
          {text: '',              value: 'viewIcon'    },
          {text: 'Title',         value: 'title'       },
          {text: 'Content',       value: 'content'     },
          {text: 'File Name',     value: 'fileName'    },
          {text: 'File Size',     value: 'fileSize'    },
          {text: 'Created',       value: 'created'     },
          {text: 'Submitter',     value: 'submitter'   },
          {text: 'Received From', value: 'receivedFrom'},
          {text: '',              value: 'deleteIcon'  }
        ],

        page: 1,
        itemsPerPage: 7,

        markForDeletionSet: new Set(),
        // Set not reactive with respect to additions and deletions...
        markForDeletionArr: [],
        overlayId: null,
        overlayContent: null
    }),

    watch: {
      itemsPerPage () {
        this.loadContactMessages();
      },

      page () {
        this.loadContactMessages();
      },

      overlayId () {
        if (!this.overlayId) {
          this.overlayContent = null;
        } else {
          this.credentialedAxios('loading full contact message',
            axios => axios.get(this.$apiUrl + '/contact-admin/full/' + this.overlayId), data => this.overlayContent = data);
        }
      }
    },

    methods: {
      countContactMessages() {
        this.credentialedAxios('counting contact messages',
          axios => axios.get(this.$apiUrl + '/contact-admin/count'), totalItems => this.totalItems = totalItems);
      },

      loadContactMessages() {
        this.credentialedAxios('loading contact messages',
          axios => axios.get(this.$apiUrl + '/contact-admin?page=' + this.page + '&size=' + this.itemsPerPage),
          data => this.data = data);
      },

      computeDownloadHref(effectiveId) {
        return this.$apiUrl + '/contact-admin/download/' + effectiveId;
      },

      itemClass(item) {
        return this.markForDeletionSet.has(item.effectiveId)
            && 'markedForDeletion'
            || '';
      },

      clearSelection() {
        this.markForDeletionSet = new Set();
        this.markForDeletionArr = [];
      },

      deleteSelected() {
        console.log('Sending for deletion', this.markForDeletionArr);
        this.credentialedAxios('deleting contact messages', axios =>
            axios.delete(this.$apiUrl + '/contact-admin?' + this.markForDeletionArr.map(id => 'effectiveId=' + encodeURIComponent(id)).join('&')),
          () => {
            this.clearSelection();
            this.countContactMessages();
            this.loadContactMessages();
          });
      }
    },

    mounted () {
      this.countContactMessages();
      this.loadContactMessages();
    }
  }
</script>
<style>
  .markedForDeletion {
    background: rgba(250, 80, 70, 0.3);
  }
</style>